// main -herader- search 
.search-from{
  
  height: 40px;
  @include breakpoint(large){
    width: 351px;
  }

.search-form {
  .form-group {
    position: relative;
    margin-bottom: 0;
    #s {
    
      padding: 9px 15px;
      border: 1px solid #000000;
      border-radius: 4px;
      background: #F8F8F8;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    .form-control:focus {
      color: #495057;
      background-color: #fff;
      border-color: #80bdff;
      outline: 0;
      box-shadow: none;
  }
  }
  .icon {
    position: absolute;
    top: 50%;
    right: 8px;
    transform: translateY(-50%);
  }
}
}
.main-menu{

  ul{
    li{
      display: inline-block;
      margin-right: 32px;
     
      a{
        font-size: 20px;
        font-family: "Jost";
        font-weight: 400;
        text-transform: uppercase;
        color: #000000;
        text-decoration: none;
        display: block;
        height: 100%;
        padding: 37px 0;
        p{
          font-size: 20px;
          font-family: $jostfamily;
          font-weight: 400;
          text-transform: uppercase;
          color: $current-Black;
        }
        .menu-arrow{
          width: 24px;
        height: 24px;
        overflow: hidden;
         img{
          display: inline-block;
          vertical-align: middle;
         }
        }
      }
      &:last-child {
        margin-right: 0;
      }
    }
    
  }
}

.user-cart-navigation{
  @include breakpoint(xlarge){
    margin-left: 30px;
  }

  @include breakpoint(xxlarge){
    margin-left: 48px;

  }
  ul{
    li{
      a{
        padding: 10px;
      }
    }
  }
}



